.custom-button {
    
    min-width: 120px;
    width: auto;
    height: 45px;
    font-size: 0.9rem;
    padding: 0 25px;
    background-color: var(--ter);
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    border: none;
    transition: all 0.2s ease-in;
    cursor: pointer;
    margin: 15px 0;

    &:hover {
        transform: scale(1.05);
    }

    &.google-sign-in {
        background-color: #4285f4;

        &:hover {
            background-color: #357ae8;
        }
    }
}
