.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid var(--ter);
    padding: 15px 0;
    font-size: 1.5rem;
    align-items: center;

    .image-container {
        width: 23%;
        padding-right: 15px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .name,
    .quantity,
    .price {
        width: 23%;
    }

    .quantity{
        display: flex;

        .arrow {
            cursor: pointer;
        }

        .value {
            margin: 0 10px;
        }
    }

    .remove-button {
        cursor: pointer;
    }

}