.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    border-bottom: 2px solid var(--ter);

    .logo-container {
        height: 100%;
        width: 70px;
        padding: 10px 15px;
    }

    .options {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        cursor: pointer;
    

        .option{
            padding: 10px 15px;
            color: black;
            font-weight: 600;
        }
    }

}